.dropdown-menu {
  display: grid;
  justify-content: center;
  margin: -5px auto 0;
  background-color: #fff;
  filter: drop-shadow(0 3px 3px rgba(0, 0, 0, 0.2));
  border-radius: 5px;
  z-index: 100; }

.dropdown-toggle {
  display: flex;
  align-items: center;
  background: none;
  border: none;
  outline: none; }
  .dropdown-toggle > .image {
    height: 50px;
    width: 50px;
    margin: 0 10px;
    border-radius: 50%; }

.dropdown-item {
  display: grid;
  width: 100%;
  padding: 20px;
  text-align: left;
  border: none;
  background: none;
  white-space: nowrap;
  transition: all .2s; }
  .dropdown-item:hover {
    background: #f0f0f0; }
  .dropdown-item .logout {
